  text-align: center; }
  .dialog-chat .dialog-user-box .dialog-user-avatar,
  .dialog-chat .dialog-add-box .dialog-user-avatar {
    padding: 8px; }
    .dialog-chat .dialog-user-box .dialog-user-avatar img,
    .dialog-chat .dialog-add-box .dialog-user-avatar img {
      width: 100px;
      border-radius: 50%; }
  .dialog-chat .dialog-user-box .dialog-add-action select,
  .dialog-chat .dialog-add-box .dialog-add-action select {
    display: block;
    width: 100%;
    height: 30px; }
  .dialog-chat .dialog-user-box .dialog-add-action button,
  .dialog-chat .dialog-add-box .dialog-add-action button {
    width: 50%;
    line-height: 30px; }
  .dialog-chat .dialog-user-box p,
  .dialog-chat .dialog-add-box p {
    margin: 0; }

.dialog-chat.dialog-fixed .dialog-chat-box {
  position: fixed;
  bottom: 0;
  right: 0; }
  .dialog-chat.dialog-fixed .dialog-chat-box.dialog-min .dialog-header,
  .dialog-chat.dialog-fixed .dialog-chat-box.dialog-min .dialog-tab {
    display: none; }
  .dialog-chat.dialog-fixed .dialog-chat-box.dialog-min .dialog-info {
    padding: 0;
    height: 30px;
    width: 100%;
    text-align: center; }
    .dialog-chat.dialog-fixed .dialog-chat-box.dialog-min .dialog-info .dialog-info-avatar {
      position: absolute;
      left: 0; }
      .dialog-chat.dialog-fixed .dialog-chat-box.dialog-min .dialog-info .dialog-info-avatar img {
        width: 30px; }
    .dialog-chat.dialog-fixed .dialog-chat-box.dialog-min .dialog-info .dialog-info-name {
      line-height: 30px; }
      .dialog-chat.dialog-fixed .dialog-chat-box.dialog-min .dialog-info .dialog-info-name p {
        display: none; }
    .dialog-chat.dialog-fixed .dialog-chat-box.dialog-min .dialog-info .dialog-message-count {
      display: block;
      position: absolute;
      left: -12px;
      top: -11px;
      line-height: 20px;
      background: #f00;
      color: #fff;
      border-radius: 50%;
      min-width: 20px; }

.dialog-chat.dialog-chat-page {
  width: 100%;
  max-width: 800px;
  position: relative;
  margin: 0 auto; }
  .dialog-chat.dialog-chat-page .dialog-chat-box,
  .dialog-chat.dialog-chat-page .dialog-chat-room {
    display: block; }
  .dialog-chat.dialog-chat-page .dialog-chat-box {
    width: 250px; }
    .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-header .dialog-action .fa-minus,
    .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-header .dialog-action .fa-close {
      display: none; }
    .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-tab .dialog-tab-box .dialog-tab-item {
      height: 448px;
      overflow-y: auto; }
      .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-tab .dialog-tab-box .dialog-tab-item::-webkit-scrollbar {
        height: 6px;
        width: 6px;
        margin-right: 5px;
        background: #f5f5f5;
        transition: all 0.3s ease-in-out;
        border-radius: 0px; }
      .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-tab .dialog-tab-box .dialog-tab-item::-webkit-scrollbar-track {
        -webkit-border-radius: 0px;
        border-radius: 0px; }
      .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-tab .dialog-tab-box .dialog-tab-item::-webkit-scrollbar-thumb {
        -webkit-border-radius: 0px;
        border-radius: 0px;
        background: rgba(0, 0, 0, 0.5); }
        .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-tab .dialog-tab-box .dialog-tab-item::-webkit-scrollbar-thumb:hover {
          background: rgba(0, 0, 0, 0.6); }
        .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-tab .dialog-tab-box .dialog-tab-item::-webkit-scrollbar-thumb:active {
          background: rgba(0, 0, 0, 0.8); }
        .dialog-chat.dialog-chat-page .dialog-chat-box .dialog-tab .dialog-tab-box .dialog-tab-item::-webkit-scrollbar-thumb:window-inactive {
          background: rgba(0, 0, 0, 0.4); }
  .dialog-chat.dialog-chat-page .dialog-chat-room {
    left: 250px; }
